import React,{ Component } from "react";
import { Link } from "react-router-dom";
import urlConst from '../config/config';
import Background from '../resources/dashboard_image.jpg';

export default class RegistrationPage extends Component{
    state={
        firstname: '',
        lastname: '',
        email:'',
        mobileNumber:'',
        password:'',
        users : [],
        validation:{
            firstname: null,
            lastname: null,
            email: null,
            mobileNumber: null,
            password: null
        }
    };

  constructor(props : any){
    super(props);
    this.formInputChange= this.formInputChange.bind(this);
    this.formSubmit= this.formSubmit.bind(this);
  }   

  
 formInputChange(event : any){
    this.setState({
        [event.target.name] : event.target.value
    });

    const validation: any = this.state.validation;
    if(!event.target.value){
        validation[event.target.name]='Can\'t be empty';
    }
    else{
        validation[event.target.name]=null;
    }

    this.setState({
        validation
    });
 }  
 
 formSubmit(event : any){
    event.preventDefault();

    const users : any[]= this.state.users;
    users.push({
        firstname: this.state.firstname,
        lastname: this.state.lastname,
        email: this.state.email,
        mobileNumber: this.state.mobileNumber,
        password: this.state.password
    })
    this.setState({
        users,
        firstname: '',
        lastname: '',
        email: '',
        mobileNumber: '',
        password: ''
    })

    const axios = require('axios'); 
    axios.post(urlConst.url +'/users', {
        firstname: users[0].firstname,
        lastname: users[0].lastname,
        email:users[0].email,
        mobileNumber: users[0].mobileNumber,
        password: users[0].password
      })
      .then((response :any) => {
        alert("Registration done successfully");
    }).catch((error: any) => {
    });
         
 }
    render(){
        return(
            <div style={{ backgroundColor: "black", backgroundImage: `url(${Background})`, height: "657px", backgroundSize: 'cover', backgroundAttachment:'fixed' }}>
            <div className='container col-5'>
                <br/><br/>
                <h3 style={{ color: "#ffe20f",position: 'fixed', left: '400px' }}>Sign Up Form</h3>
                <br/><br/>
                <br/><br/>
                <form onSubmit={this.formSubmit}>                
                    <div className='form-row mb-3'>
                        <input type='text' name='firstname' placeholder='First Name' className='form-control color' value={this.state.firstname}   
                        onChange={this.formInputChange}/>
                        {this.state.validation.firstname ? <span style={{color:'red'}}>{this.state.validation.firstname}</span> : null}
                    </div>
                    <div className='form-row mb-3'>
                        <input type='text' name='lastname' placeholder='Last Name' className='form-control' value={this.state.lastname} 
                        onChange={this.formInputChange}/>
                        {this.state.validation.lastname ? <span style={{color:'red'}}>{this.state.validation.lastname}</span> : null}
                    </div>
                    <div className='form-row mb-3'>
                        <input type='text' name='email' placeholder='Email' className='form-control' value={this.state.email} 
                        onChange={this.formInputChange}/>
                        {this.state.validation.email ? <span style={{color:'red'}}>{this.state.validation.email}</span> : null}
                    </div>
                    <div className='form-row mb-3'>
                        <input type='text' name='mobileNumber' placeholder='Mobile Number' className='form-control' value={this.state.mobileNumber}
                        onChange={this.formInputChange}/>
                        {this.state.validation.mobileNumber ? <span style={{color:'red'}}>{this.state.validation.mobileNumber}</span> : null}
                    </div>
                    <div className='form-row mb-3'>
                        <input type='text' name='password' placeholder='Password' className='form-control' value={this.state.password}
                        onChange={this.formInputChange}/>
                        {this.state.validation.password ? <span style={{color:'red'}}>{this.state.validation.password}</span> : null}
                    </div>   

                    <div className='form-row mb-3'>
                        <button type='submit' className='btn btn-success'>Confirm</button>
                        <Link to="/" className='btn btn-primary ml-5'>Go to Home </Link>
                    </div>
                </form>                
            </div>  </div>
        )
    }
}